import React, { Component } from 'react';
import { Button, ButtonGroup, Container, Table } from 'reactstrap';
import AppNavbar from './AppNavbar';
import { Link } from 'react-router-dom';

import AlertMessage from './AlertMessage';

import '../assets/css/TodoList.css';

const URL = `/api/todo`;

class TodoList extends Component {
  constructor(props) {
    super(props);
    this.state = { todos: [], isLoading: true, expireTodo: [] };
    this.remove = this.remove.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.getFormatDate = this.getFormatDate.bind(this);
  }

  componentDidMount() {
    this.setState({ isLoading: true });

    fetch(URL)
      .then(response => response.json())
      .then(data => {
        this.setState({ todos: data, isLoading: false });

        // ÎßàÍ∞êÍ∏∞Ìïú ÏßÄÎÇú todo Îì±Î°ù
        this.state.todos.forEach(todo => {
          if (new Date(todo.dueDate) < new Date() && todo.dueDate !== null) {
            this.setState({
              expireTodo: this.state.expireTodo.concat({ ...todo }),
            });
          }
        });
      })
      .catch(err => {
        console.error(`error! : ${err}`);
        alert('ÏÑúÎ≤ÑÏóêÏÑú Î∂àÎü¨Ïò§ÎäîÎç∞ Ïã§Ìå®ÌñàÏäµÎãàÎã§.');
      });
  }

  /* Todo ÏÇ≠Ï†ú*/
  async remove(id) {
    await fetch(`${URL}/${id}`, {
      method: 'DELETE',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
      .then(() => {
        let updatedTodo = [...this.state.todos].filter(i => i.id !== id);
        this.setState({ todos: updatedTodo });
      })
      .catch(err => {
        console.error(`error! : ${err}`);
        alert('ÏÇ≠Ï†úÌïòÎäîÎç∞ Ïã§Ìå®ÌïòÏòÄÏäµÎãàÎã§.');
      });
  }

  /* ÎßàÍ∞êÍ∏∞Ìïú ÎÇ†Ïßú Ï≤òÎ¶¨ */
  getFormatDate(dateTime) {
    if (dateTime === null) return false;
    const date = new Date(dateTime);
    const year = date.getFullYear();

    let month = date.getMonth() + 1;
    month = month >= 10 ? month : '0' + month;

    let day = date.getDate();
    day = day >= 10 ? day : '0' + day;

    let hours = date.getHours();
    hours = hours >= 10 ? hours : '0' + hours;

    let minutes = date.getMinutes();
    minutes = minutes >= 10 ? minutes : '0' + minutes;

    return `${year}ÎÖÑ ${month}Ïõî ${day}Ïùº ${hours}Ïãú ${minutes}Î∂Ñ`;
  }

  /* Done Ï≤òÎ¶¨ */
  async handleClick(event, id) {
    event.preventDefault();
    let item = this.state.todos.filter(todo => todo.id === id)[0];

    if (event.target.tagName === 'SPAN') {
      const eventTarget = event.target.parentElement;

      /* Î≤ÑÌäº Î≥ÄÍ≤Ω target */
      const parent = eventTarget.parentElement.parentElement;
      const targetButton =
        parent.lastElementChild.firstElementChild.firstElementChild;

      if (eventTarget.className !== 'done') {
        eventTarget.className = 'done';
        eventTarget.innerText = '‚≠ïÔ∏è';

        /* ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
        parent.className = 'text-success font-weight-bold mark';

        /* Î≤ÑÌäº Î≥ÄÍ≤Ω */
        if (!targetButton.classList.contains('d-none'))
          targetButton.classList.add('d-none');
      } else {
        eventTarget.className = 'doing';
        eventTarget.innerText = '‚ùå';

        /* ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
        parent.className = '';

        /* Î≤ÑÌäº Î≥ÄÍ≤Ω */
        if (targetButton.classList.contains('d-none'))
          targetButton.classList.remove('d-none');
      }
    }
    if (event.target.className !== 'done' && event.target.tagName === 'A') {
      /* Î≤ÑÌäº Î≥ÄÍ≤Ω target */
      const parent = event.target.parentElement.parentElement;
      const targetButton =
        parent.lastElementChild.firstElementChild.firstElementChild;

      event.target.className = 'done';
      event.target.innerText = '‚≠ïÔ∏è';

      /* ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
      parent.className = 'text-success font-weight-bold mark';

      /* Î≤ÑÌäº Î≥ÄÍ≤Ω */
      if (!targetButton.classList.contains('d-none'))
        targetButton.classList.add('d-none');
    } else if (event.target.tagName === 'A') {
      /* Î≤ÑÌäº Î≥ÄÍ≤Ω target */
      const parent = event.target.parentElement.parentElement;
      const targetButton =
        parent.lastElementChild.firstElementChild.firstElementChild;

      event.target.className = 'doing';
      event.target.innerText = '‚ùå';

      /* ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
      parent.className = '';

      /* Î≤ÑÌäº Î≥ÄÍ≤Ω */
      if (targetButton.classList.contains('d-none'))
        targetButton.classList.remove('d-none');
    }

    item.done = !item.done;

    await fetch(`${URL}/${id}`, {
      method: 'PUT',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(item),
    }).catch(err => {
      console.error(`ÏàòÏ†ïÌïòÎäîÎç∞ Ïã§Ìå®ÌïòÏòÄÏäµÎãàÎã§. ${err}`);
      alert('ÏàòÏ†ïÌïòÎäîÎç∞ Ïã§Ìå®ÌïòÏòÄÏäµÎãàÎã§.');
    });
  }

  render() {
    const { todos, isLoading, expireTodo } = this.state;

    /* loading page */
    if (isLoading) {
      return <p>Loading...</p>;
    }

    const msg = expireTodo.map(exTodo => {
      return (
        <AlertMessage
          key={exTodo.id}
          message={`"${exTodo.title}"Í∞Ä ÎßåÎ£åÎêòÏóàÏäµÎãàÎã§.`}
        />
      );
    });

    /* Ïö∞ÏÑ†ÏàúÏúÑ Ï†ïÎ†¨ */
    todos.sort((o1, o2) => {
      if (o1.sequence > o2.sequence) return 1;
      else if (o1.sequence < o2.sequence) return -1;
      else return 0;
    });

    /* Todo List ÎÇ¥Ïö© Ï≤òÎ¶¨ */
    const todoList = todos.map(todo => {
      return (
        <tr
          key={todo.id}
          className={todo.done ? 'text-success font-weight-bold mark' : ''}>
          <td>
            <a
              className={todo.done ? 'done' : 'doing'}
              href="#"
              onClick={e => this.handleClick(e, todo.id)}>
              <span role="img" aria-label="emoji">
                {todo.done ? '‚≠ïÔ∏è' : '‚ùå'}
              </span>
            </a>
          </td>
          <td style={{ whiteSpace: 'nowrap' }}>{todo.title}</td>
          {/* ÎÇ¥Ïö© */}
          <td>{todo.content}</td>
          {/* ÎßàÍ∞êÍ∏∞Ìïú */}
          <td>{this.getFormatDate(todo.dueDate)}</td>
          {/* Ïö∞ÏÑ†ÏàúÏúÑ */}
          <td>{todo.sequence}</td>
          <td>
            <ButtonGroup>
              <Button
                className={todo.done ? 'd-none' : ''}
                size="sm"
                color="primary"
                tag={Link}
                to={'/todo/' + todo.id}>
                Edit
              </Button>
              <Button
                size="sm"
                color="danger"
                onClick={() => this.remove(todo.id)}>
                Delete
              </Button>
            </ButtonGroup>
          </td>
        </tr>
      );
    });

    return (
      <div>
        <AppNavbar />
        <Container fluid>
          {/* Add button*/}
          <div className="float-right">
            <Button color="success" tag={Link} to="/todo/new">
              Add todo{' '}
              <span role="img" aria-label="emoji">
                üòé
              </span>
            </Button>
          </div>
          <h2 className="mt-3 mb-5">
            <span role="img" aria-label="emoji">
              üßò‚Äç‚ôÇÔ∏è
            </span>{' '}
            My Todo List
          </h2>
          {/* Table column Ïù¥Î¶Ñ */}
          <Table className="mt-4">
            <thead>
              <tr>
                <th width="5%">done</th>
                <th width="20%">Ï†úÎ™©</th>
                <th width="20%">ÎÇ¥Ïö©</th>
                <th>ÎßàÍ∞ê Í∏∞Ìïú</th>
                <th>Ïö∞ÏÑ†ÏàúÏúÑ</th>
                <th width="10%">Actions</th>
              </tr>
            </thead>
            <tbody>{todoList}</tbody>
          </Table>
          {/* Alert Message */}
          <div className="asd">{msg}</div>
        </Container>
      </div>
    );
  }
}

export default TodoList;
